import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router';
const routes: Array<RouteRecordRaw> = [
  {
    path: '/initView',
    name: 'initView',
    component: () => import('@/views/stencil/initView.vue')
  },
  {
    path: '/tsView',
    name: 'tsView',
    component: () => import('@/views/stencil/tsView.vue')
  },
  {
    path: '/echarts',
    name: 'echarts',
    component: () => import('@/views/echarts/index.vue')
  },
  {
    path: '/devextreme',
    name: 'devextreme',
    component: () => import('@/views/stencil/devextreme.vue')
  },
  {
    path: '/customDev',
    name: 'customDev',
    component: () => import('@/views/stencil/customDev.vue')
  },
  {
    path: '/asDemo',
    name: 'asDemo',
    component: () => import('@/views/stencil/asDemo.vue')
  },
  {
    path: '/ioHome',
    name: 'ioHome',
    component: () => import('@/views/ioHome.vue')
  }
  
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

export default router;
