import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import 'devextreme/dist/css/dx.common.css'
import 'devextreme/dist/css/dx.light.css'//此为组件主题样式，可修改dx.dark、dx.light、dx.darkmoon等，详情见官方API


import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'

import ArcoVue from '@arco-design/web-vue';
import '@arco-design/web-vue/dist/arco.css';

import '@/styles/index.scss';

// markdown
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'

// 创建应用实例对象--app，类似于vue2中的vm，但app比vm更'轻'
const app = createApp(App);
// console.log('app', app);
app.config.errorHandler = (err, vm, info) => {
  // 处理错误
  // `info` 是 Vue 特定的错误信息，比如错误所在的生命周期钩子
  console.log('err', err);
}
app.use(router).use(store).use(ElementPlus).use(ArcoVue).use(mavonEditor).mount('#app');

// markdown 样式
import hljs from 'highlight.js'
import 'highlight.js/styles/googlecode.css' //样式文件
app.directive('highlight', function(el) {
  const blocks = el.querySelectorAll('pre code');
  blocks.forEach((block: HTMLElement) => {
    hljs.highlightBlock(block)
  })
})

import axios from 'axios';
app.config.globalProperties.$axios = axios;

import 'devextreme/dist/css/dx.common.css'
import 'devextreme/dist/css/dx.light.css'//此为组件主题样式，可修改dx.dark、dx.light、dx.darkmoon等，详情见官方API
